import React, { FC, PropsWithChildren, forwardRef } from 'react';

import { Switch as MantineSwitch, SwitchProps as MantineSwitchProps, MantineTheme } from '@mantine/core';

import { Text } from '../Text/Text';
import { EventHandler, FormattedColor, Size, TwBaseProps } from '../../types';
import { SwitchStyleComputer } from './SwitchStyleComputer';
import { styled } from '../../utils';

export type SwitchVariants = 'simple' | 'short';

export interface SwitchProps
  extends TwBaseProps,
    PropsWithChildren,
    EventHandler<React.InputHTMLAttributes<HTMLInputElement>>,
    Pick<
      MantineSwitchProps,
      'checked' | 'labelPosition' | 'label' | 'description' | 'disabled' | 'thumbIcon'
    > {
  variant?: SwitchVariants;
  size?: Size;
  color?: FormattedColor;
  // onChange: (...args: any) => any;
}

const Root = styled(MantineSwitch)<MantineSwitchProps>(() => ({}));

export const Switch: FC<SwitchProps> = forwardRef((props, ref) => {
  const {
    size = 'md',
    'data-testid': dataTestId = 'switch',
    labelPosition = 'right',
    label,
    variant = 'simple',
    ...other
  } = props;

  return (
    <Root
      {...{ size, labelPosition, 'data-testid': dataTestId, ...other }}
      label={label ? <Text>{label}</Text> : null}
      styles={(theme: MantineTheme) => {
        const switchStyle = new SwitchStyleComputer(theme, props);

        return {
          track: switchStyle.track_CustomStyles,
          thumb: switchStyle.thumb_CustomStyles,
        };
      }}
      ref={ref}
    />
  );
});
