import { FormattedColor, Size } from '../../types';
import { SwitchProps, SwitchVariants } from './Switch';
import { MantineTheme } from '@mantine/core';

type ExtractedSwitchProps = Pick<SwitchProps, 'size' | 'color' | 'variant' | 'checked'>;

export class SwitchStyleComputer {
  private readonly size: Size = 'md';
  private readonly color: FormattedColor = 'one.6';
  private readonly variant: SwitchVariants = 'simple';
  private readonly checked: boolean = false;

  public constructor(private readonly theme: MantineTheme, props: ExtractedSwitchProps) {
    this.size = props.size ?? this.size;
    this.color = props.color ?? this.color;
    this.variant = props.variant ?? this.variant;
    this.checked = props.checked ?? this.checked;
  }

  // convert the formatted color to the actual color string it represents in the theme object
  private get colorString(): string {
    const [clr, shade] = this.color.split('.'); // like "orange.5" for example
    return this.theme.colors[clr][+shade];
  }

  private get shortTrack(): object | null {
    if (this.variant !== 'short') return null;
    const trackSize = {
      xs: { width: '1.125rem', height: '0.5rem' },
      sm: { width: '1.75rem', height: '0.75rem' },
      md: { width: '2.25rem', height: '1rem' },
      lg: { width: '2.875rem', height: '1.25rem' },
      xl: { width: '3.375rem', height: '1.5rem' },
    }[this.size];
    return {
      ...trackSize,
      overflow: 'visible',
      minWidth: 'unset',
    };
  }

  private get shortThumb(): object | null {
    if (this.variant !== 'short') return null;
    const thumbSize = {
      xs: '0.625rem',
      sm: '1rem',
      md: '1.25rem',
      lg: '1.5rem',
      xl: '1.875rem',
    }[this.size];
    return {
      width: thumbSize,
      height: thumbSize,
      left: this.checked ? `calc(100% - ${thumbSize}) !important` : 0,
      border: `1px solid ${this.theme.colors.gray[2]} !important`,
    };
  }

  // currently using the format for Mantine custom styles based on their style api for the switch component
  public get track_CustomStyles() {
    return {
      border: 'none',
      ':active': { boxShadow: `0px 0px 0px 2px #FFFFFF, 0px 0px 0px 4px ${this.colorString}` },
      ...(!!this.shortTrack && this.shortTrack),
    };
  }

  public get thumb_CustomStyles() {
    return {
      border: 'none',
      boxShadow: '0px 1px 3px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.06)',
      ...(!!this.shortThumb && this.shortThumb),
    };
  }
}
